import React, { Component } from "react";
import {
  Segment,
  Form,
  Grid,
  Button,
  Header,
  Icon,
  Message,
  Transition,
  Divider
} from "semantic-ui-react";
import axios from "axios";
import { Redirect } from "react-router-dom";
// import jwtDecode from "jwt-decode";

import "../style/App.css";
import setAuth from "../js/setAuth";

const API = "http://68.183.230.159:8081";

export default class Login extends Component {
  constructor(props) {
    super(props);
    this.state = {
      username: "",
      password: "",
      name: "",
      university: "",
      loading: false,
      wrong: false,
      newUser: false,
      redirect: "",
      visible: false
    };
  }

  componentDidMount() {
    document.title = "ICEM 2020";
    // check if user is already logged in
    if (localStorage.accessToken) {
      this.checkLogin();
    }
    setTimeout(() => {
      console.log("loading...");
      this.setState({ visible: true });
    }, 500);
  }

  login = e => {
    e.preventDefault();
    this.setState({ loading: true });
    axios
      .post(API + "/login", this.state)
      .then(res => {
        // store Token on localstorage
        const token = res.data.token;
        localStorage.setItem("accessToken", token);
        setAuth(token);
        this.setState({ loading: false });
        // console.log(jwtDecode(localStorage.accessToken));
        this.checkLogin();
      })
      .catch(err => {
        this.setState({
          loading: false,
          wrong: true
        });
      });
  };

  register = e => {
    e.preventDefault();
    this.setState({ loading: true });
    axios
      .post(API + "/register?team=" + this.state.username, {
        name: this.state.name,
        university: this.state.university
      })
      .then(res => {
        // console.log(res);
        this.setState({
          newUser: false,
          loading: false
        });
      })
      .catch(err => {
        console.log(err);
        this.setState({ loading: false });
      });
  };

  checkLogin() {
    // console.log("Check for logged in.");
    this.setState({ loading: true });
    axios.get(API + "/users/profile").then(res => {
      // console.log(res);
      if (res.data.team_name === "" && res.data.university === "") {
        this.setState({
          username: res.data.id,
          loading: false,
          newUser: true
        });
      } else {
        this.setState({ redirect: "/lobby" });
      }
    });
  }

  userLogout = e => {
    this.setState({ loading: false });
    localStorage.removeItem("accessToken");
    window.location.reload(false);
  };

  render() {
    if (this.state.redirect) {
      return <Redirect to={this.state.redirect} />;
    }
    return (
      <div className="App">
        {this.state.newUser ? (
          <header className="App-header">
            <Grid className="menu-bar">
              <Grid.Column floated="right" width={5}>
                <Button
                  floated="right"
                  size="big"
                  icon="sign-out"
                  type="submit"
                  onClick={this.userLogout}
                  color="red"
                />
              </Grid.Column>
            </Grid>

            <div className="header">
              <Header as="h1" icon className="white-txt">
                <Icon name="pencil alternate" color="teal" />
                REGISTER
                <Header.Subheader className="white-txt">
                  Edit your information
                </Header.Subheader>
              </Header>
            </div>

            <Segment placeholder>
              <Grid relaxed="very" stackable>
                <Grid.Column>
                  <Form onSubmit={this.register} loading={this.state.loading}>
                    <Form.Input
                      icon="user md"
                      iconPosition="left"
                      label="Username"
                      value={this.state.username}
                      readOnly
                    />
                    <Form.Input
                      required
                      type="text"
                      icon="edit"
                      iconPosition="left"
                      label="Team name"
                      placeholder="Team name"
                      value={this.state.name}
                      onChange={e => this.setState({ name: e.target.value })}
                    />
                    <Form.Input
                      required
                      type="text"
                      icon="building"
                      iconPosition="left"
                      label="University"
                      placeholder="University"
                      value={this.state.university}
                      onChange={e =>
                        this.setState({ university: e.target.value })
                      }
                    />
                    <Button
                      type="submit"
                      content="Continue"
                      disabled={this.state.loading}
                      primary
                    />
                  </Form>
                </Grid.Column>
              </Grid>
            </Segment>
          </header>
        ) : (
          <header id="login" className="App-header">
            <Transition
              visible={this.state.visible}
              animation="scale"
              duration={500}
            >
              <div>
                <Header as="h1" icon className="white-txt">
                  <Icon name="heartbeat" color="teal" />
                  ICEM 2020
                  <Header.Subheader className="white-txt">
                    Sign in to start your challenge
                  </Header.Subheader>
                </Header>
              </div>
            </Transition>

            <Form error={this.state.wrong}>
              <Message
                error
                onDismiss={e => this.setState({ wrong: false })}
                header="Please try again!"
                content="Invalid Username or Password"
              />
            </Form>

            <Transition
              visible={this.state.visible}
              animation="scale"
              duration={500}
            >
              <Segment placeholder>
                <Grid relaxed="very" stackable>
                  <Grid.Column>
                    <Form
                      error
                      onSubmit={this.login}
                      loading={this.state.loading}
                    >
                      <Form.Input
                        type="text"
                        icon="user md"
                        iconPosition="left"
                        label="Username"
                        placeholder="Username"
                        value={this.state.username}
                        onChange={e =>
                          this.setState({ username: e.target.value })
                        }
                      />
                      <Form.Input
                        type="password"
                        icon="lock"
                        iconPosition="left"
                        label="Password"
                        placeholder="Password"
                        value={this.state.password}
                        onChange={e =>
                          this.setState({ password: e.target.value })
                        }
                      />
                      <Divider hidden />
                      <Button
                        type="submit"
                        content="Sign in"
                        size="large"
                        disabled={this.state.loading}
                        color="green"
                      />
                    </Form>
                  </Grid.Column>
                </Grid>
              </Segment>
            </Transition>
          </header>
        )}
      </div>
    );
  }
}
