import React, { Component } from "react";
import { Redirect } from "react-router-dom";
import {
  Segment,
  Form,
  Grid,
  Button,
  Header,
  Icon,
  Divider
} from "semantic-ui-react";
import "../style/App.css";
import axios from "axios";
import setAuth from "../js/setAuth";

const API = "https://icem-2020.herokuapp.com";

export default class Login extends Component {
  constructor(props) {
    super(props);
    this.state = {
      username: "",
      name: "",
      university: "",
      loading: false
    };
    this.checkLogin = this.checkLogin.bind(this);
  }

  componentDidMount() {
    document.title = "ICEM2020 - Register";
    if (localStorage.accessToken) {
      this.checkLogin();
    }
  }

  login = e => {
    e.preventDefault();
    console.log("entering login state.");
    this.setState({ loading: true });
    axios
      .post(API + "/login", this.state)
      .then(res => {
        // store Token on local
        const token = res.data.token 
        localStorage.setItem('accessToken', token);
        setAuth(token);
        this.setState({ loading: false })
        // console.log(jwtDecode(localStorage.accessToken));
        this.checkLogin();
      })
      .catch(err => {
        this.setState({ loading: false });
        this.setState({ wrong: true });
      });
  };

  // register function for first time login user
  register = e => {
    e.preventDefault();
    this.setState({ loading: true });
    console.log("registering");
    axios
      .post(API + "/register?team=" + this.state.username, {
        name: this.state.name,
        university: this.state.university
      })
      .then(res => {
        this.setState({ redirect: "/lobby"});
      })
      .catch(err => {
        console.log("register error!");
        console.log(err);
        this.setState({ loading: false });
      });
  };

  checkLogin() {
    this.setState({ loading: true });
    axios
      .get(API + "/users/profile")
      .then(res => {
        console.log(res);
        this.setState({ username: res.data.id });
        this.setState({ loading: false });
      })
      .catch(err => {
        console.log("New user detected!");
      });
  }

  logout = e => {
    localStorage.removeItem("accessToken");
    this.setState({ loading: true });
    this.setState({ redirect: "/"});
  };

  render() {
    if (this.state.redirect) {
      return <Redirect to={this.state.redirect} />
    }
    return (
      <div className="App">
        <header className="App-header">
          <div className="header">
            <Header as="h1" icon className="white-txt">
              <Icon name="pencil alternate" />
              REGISTER
              <Header.Subheader className="white-txt">
                Edit your information
              </Header.Subheader>
            </Header>
          </div>

          <Segment placeholder>
            <Grid relaxed="very" stackable>
              <Grid.Column>
                <Form onSubmit={this.register} loading={this.state.loading}>
                  <Form.Input
                    icon="user md"
                    iconPosition="left"
                    label="Username"
                    value={this.state.username}
                    readOnly
                  />
                  <Form.Input
                    type="text"
                    icon="edit"
                    iconPosition="left"
                    label="Team name"
                    placeholder="Team name"
                    value={this.state.name}
                    onChange={e => this.setState({ name: e.target.value })}
                  />
                  <Form.Input
                    type="text"
                    icon="building"
                    iconPosition="left"
                    label="University"
                    placeholder="University"
                    value={this.state.university}
                    onChange={e =>
                      this.setState({ university: e.target.value })
                    }
                  />
                  <Button
                    type="submit"
                    content="Continue"
                    disabled={this.state.loading}
                    primary
                  />
                  <Divider />
                  <Button
                    type="submit"
                    content="Sign out"
                    disabled={this.state.loading}
                    onClick={this.logout}
                    color="red"
                  />
                </Form>
              </Grid.Column>
            </Grid>
          </Segment>
        </header>
      </div>
    );
  }
}
