import React, { Component } from "react";
import {
  Button,
  Segment,
  Grid,
  Divider,
  Header,
  Icon,
  Statistic,
} from "semantic-ui-react";
import axios from "axios";
import { Redirect } from "react-router-dom";

import "../style/App.css";

const API = "http://68.183.230.159:8081";

export default class Login extends Component {
  constructor(props) {
    super(props);
    this.state = {
      username: "",
      team_name: "",
      university: "",
      x2: false,
      x3: false,
      loading: false,
      lobby: false,
      score: 0,
      white_board: "",
      start_time: "",
      count_time: "",
      timeout: "",
      exam: "",
      round: "",
      redirect: ""
    };
  };

  componentDidMount() {
    document.title = "ICEM 2020 - Lobby";
    // check if user already logged in
    if (localStorage.accessToken) {
      this.checkLogin();
    } else {
      console.log("Token not found!");
      window.location.href = "/";
    }
  }

  handleX2 = e => {
    this.setState(prevState => ({ x2: !prevState.x2 }));
  };

  handleX3 = e => {
    this.setState(prevState => ({ x3: !prevState.x3 }));
  };

  submit = e => {
    this.setState({ loading: true, redirect: "/whiteboard" });
  };

  checkLogin() {
    this.setState({ loading: true });
    axios.get(API + "/users/profile").then(res => {
      this.setState({
        username: res.data.id,
        team_name: res.data.team_name,
        university: res.data.university
      });
      this.getData();
    });
  }

  // check for a game in session
  getData() {
    this.setState({ loading: true });
    axios
      .get(API + "/game/session/data")
      .then(res => {
        console.log(res);
        if (res.data.round === null) {
          this.setState({ loading: false });
        } else if (res.data.white_board === "true" && res.data.round != null) {
          this.setState({
            lobby: true,
            loading: false
          });
        } else {
          this.setState({
            white_board: res.data.white_board,
            start_time: res.data.start_time,
            count_time: res.data.count_time,
            timeout: res.data.timeout,
            exam: res.data.exam,
            round: res.data.round,
            loading: false
          });
          this.getScore();
        }
      })
      .catch(err => {
        console.log(err);
      });
  }

  getScore() {
    axios.get(API + "/users/score/" + this.state.round).then(res => {
      this.setState({
        score: res.data.total_score,
        loading: false
      });
    });
  }

  userLogout = e => {
    this.setState({ loading: true });
    localStorage.removeItem("accessToken");
    window.location.reload(false);
  };

  render() {
    if (this.state.redirect) {
      return <Redirect to={this.state.redirect} />;
    }
    return (
      <div className="App">
        {this.state.loading ? (
          <header className="App-header">
            <Header as="h1" icon className="white-txt">
              Waiting for session...
            </Header>
          </header>
        ) : (
          <div>
            {this.state.lobby ? (
              <header className="App-header">
                <div className="header">
                  <Header as="h1" icon className="white-txt">
                    <Icon name="heartbeat" color="teal" />
                    {this.state.round.toUpperCase() + " ROUND"}
                    <Header.Subheader className="white-txt">
                      {this.state.university + " @ ICEM 2020"}
                    </Header.Subheader>
                    <Header.Subheader className="white-txt">
                      {"Score: " + this.state.score}
                    </Header.Subheader>
                  </Header>
                </div>
                <Segment
                  placeholder
                  className="lobby-size"
                  loading={this.state.loading}
                >
                  <Grid columns={2} relaxed="very" stackable>
                    <Grid.Column>
                      <Header icon>
                        X2
                        <Icon
                          disabled={this.state.x3}
                          onClick={this.handleX2}
                          name={this.state.x2 ? "toggle on" : "toggle off"}
                          color={this.state.x2 ? "green" : "black"}
                        />
                        {this.state.x2 ? "Enable" : "Disable"}
                      </Header>
                    </Grid.Column>

                    <Grid.Column verticalAlign="middle">
                      <Header icon>
                        X3
                        <Icon
                          disabled={this.state.x2}
                          onClick={this.handleX3}
                          name={this.state.x3 ? "toggle on" : "toggle off"}
                          color={this.state.x3 ? "green" : "black"}
                        />
                        {this.state.x3 ? "Enable" : "Disable"}
                      </Header>
                    </Grid.Column>
                  </Grid>

                  <Divider vertical>Or</Divider>
                </Segment>

                <br></br>

                <Button
                  color="green"
                  size="massive"
                  onClick={this.submit}
                  disabled={this.state.loading}
                  content={this.state.loading ? "Waiting..." : "Continue"}
                />
              </header>
            ) : (
              <header className="App-header">
                
                <Grid className="menu-bar">
                  <Grid.Column floated="right" width={5}>
                    <Button
                      floated="right"
                      size="big"
                      icon="sign-out"
                      type="submit"
                      onClick={this.userLogout}
                      color="red"
                    />
                  </Grid.Column>
                </Grid>

                <div className="header">
                  <Header as="h1" icon className="white-txt">
                    <Icon name="heartbeat" color="teal" />
                    ICEM 2020
                    <Header.Subheader className="white-txt">
                      {"Team: " + this.state.team_name}
                    </Header.Subheader>
                    <Header.Subheader className="white-txt">
                      {"University: " + this.state.university}
                    </Header.Subheader>
                  </Header>
                </div>

                <Statistic inverted color="red">
                  <Statistic.Label className="score-size">
                    Score
                  </Statistic.Label>
                  <Statistic.Value color="teal">
                    {this.state.score}
                  </Statistic.Value>
                </Statistic>

                <Button
                  color="green"
                  size="massive"
                  onClick={this.submit}
                  disabled={this.state.loading}
                  content={this.state.loading ? "Waiting..." : "Continue"}
                />
              </header>
            )}
          </div>
        )}
      </div>
    );
  }
}
