import React, { Component } from "react";
import { Header, Button, Grid, Statistic, Segment, Dimmer, Icon } from "semantic-ui-react";
import { Redirect } from "react-router-dom";
import CanvasDraw from "react-canvas-draw";
import axios from "axios";

import "../style/App.css";

const API = "http://68.183.230.159:8081";

export default class Prepare extends Component {
  constructor(props) {
    super(props);
    this.state = {
      loading: false,
      brushRadius: 2.5,
      lazyRadius: 0,
      score: 0,
      active: false
    };
  }

  componentDidMount() {
    document.title = "ICEM 2020 - Whiteboard";
    // check if user already logged in
    // if (localStorage.accessToken) {
    //   this.checkLogin();
    // } else {
    //   console.log("Token not found!");
    //   this.setState({ redirect: "/"})
    // }
  }

  checkLogin() {
    console.log("Check for logged in.");
    this.setState({ loading: true });
    axios.get(API + "/users/profile").then(res => {
      // console.log(res);
      this.setState({
        username: res.data.id,
        team_name: res.data.team_name,
        university: res.data.university,
        loading: false
      });
    });
  }

  submit = e => {
    this.handleShow();
    setTimeout(() => {
      this.setState ({ redirect: "/lobby" });
    }, 2000);
  }

  handleShow = () => this.setState({ active: true })
  handleHide = () => this.setState({ active: false })

  render() {
    if (this.state.redirect) {
      return <Redirect to={this.state.redirect} />;
    }
    return (
      <div className="App">
        <Dimmer.Dimmable as={Segment} dimmed={this.state.active} className="dimmer-custom">
          <Dimmer active={this.state.active} onClickOutside={this.handleHide} >
          <Header as='h2' icon inverted>
              <Icon name='check circle outline' />
              Submitted!
            </Header>
          </Dimmer>
        
        <header className="App-header">
          <div id="header" className="header-wb">
            <Grid className="header-wb">
              <Grid.Column floated="left" width={5} className="center-vert">
              <Button
                  content="Clear"
                  icon="remove"
                  floated="left"
                  size="big"
                  color="red"
                  onClick={() => {
                    this.Canvas.clear();
                  }}
                />
                <Button
                  content="Undo"
                  icon="undo"
                  floated="left"
                  size="big"
                  color="blue"
                  onClick={() => {
                    this.Canvas.undo();
                  }}
                />
              </Grid.Column>
              <Grid.Column width={5} className="text center-vert center">
                <Header as="h1" icon className="white-txt header-wb">
                  Time Left: 90 sec
                  <Header.Subheader className="white-txt score-size">
                    {"Team: KKU"}
                  </Header.Subheader>
                </Header>
              </Grid.Column>
              <Grid.Column floated="right" width={5} className="center-vert">
              <Statistic inverted color="red" horizontal floated="right" >
                  <Statistic.Value color="teal">
                    {this.state.score}
                  </Statistic.Value>
                  <Statistic.Label className="score-size">
                    Score 
                  </Statistic.Label>
                </Statistic>
              </Grid.Column>
            </Grid>
          </div>

          <div className="whiteboard-size">
            <CanvasDraw
              ref={canvasDraw => (this.Canvas = canvasDraw)}
              className="whiteboard-size"
              lazyRadius={this.state.lazyRadius}
              brushRadius={this.state.brushRadius}
            />
          </div>

          <div id="footer" className="footer-wb">
            <Grid className="footer-wb">
              <Grid.Column floated="left" width={5}></Grid.Column>
              <Grid.Column width={5} className="center">
                <Button
                  color="green"
                  size="massive"
                  onClick={this.submit}
                  disabled={this.state.loading}
                  content={this.state.loading ? "Loading..." : "Submit"}
                />
              </Grid.Column>
              <Grid.Column floated="right" width={5}></Grid.Column>
            </Grid>
          </div>
        </header>
        </Dimmer.Dimmable>
      </div>
    );
  }
}
